#navbar{
    position: fixed;
    background: white;
    width: 100%;
    top: 0;
    z-index: 1;
    -webkit-box-shadow: 9px -4px 34px 4px rgba(0,0,0,0.44);
    -moz-box-shadow: 9px -4px 34px 4px rgba(0,0,0,0.44);
    box-shadow: 9px -4px 34px 4px rgba(0,0,0,0.44);
  }
  
  #navbar ul{
    text-align: center;
    padding: 10px;
  }
  
  #navbar li {
    display: inline;
    margin: 10px;
    padding-top: 8px;
    /*margin-bottom: 15px;*/
  }
  
  #navbar a{
    font-size: 1.2em;
    font-weight: 600;
    padding: 10px;
  }

  @media(max-width: 700px){
    #navbar li {
      display: inline-block;
    }
  }